								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" gaevent="imt/selectCity/zhongxian" href="//i.meituan.com/zhongxian/" data-citypinyin="zhongxian"></a>
								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" gaevent="imt/selectCity/zherong" href="//i.meituan.com/zherong/" data-citypinyin="zherong"></a>
								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" gaevent="imt/selectCity/zhongjiangxian" href="//i.meituan.com/zhongjiangxian/" data-citypinyin="zhongjiangxian"></a>
								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" gaevent="imt/selectCity/zhenxiongxian" href="//i.meituan.com/zhenxiongxian/" data-citypinyin="zhenxiongxian"></a>
								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" gaevent="imt/selectCity/zhouzhuang" href="//i.meituan.com/zhouzhuang/" data-citypinyin="zhouzhuang"></a>
								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" gaevent="imt/selectCity/zhanghua" href="//i.meituan.com/zhanghua/" data-citypinyin="zhanghua"></a>
								</li><li class="vmc-1px-b-DDD8CE" ><a class="react" href="//i.meituan.com/index/changecity/more/Z" data-more="true" gaevent="imt/selectCity/more"></a>
							</li></ul>
						</div>
				</div>
			</div>
		</transition>
		<app-footer></app-footer>
	</div>
</template>

<script>
	import AppHeader from '@/components/app-header'
	import AppFooter from '@/components/app-footer'
	import { mapGetters, mapActions } from 'vuex'
	import Toucheffect from '@/directives/toucheffect'
	export default {
		data() {
			return {
				heightArrList:[],
				iconlist: [
					{
						name: '',
						type: '',
						routename: 'home'
					},
					{
						name: '',
						type: '',
						routename: "search"
					}
				],
				letterlist: ['A','B','C','D','E','F','G','H','J','K','L','M','N','P','Q','R','S','T','W','X','Y','Z']
			}
		},
		directives: {
			Toucheffect
		},
		created() {
			// this.getCitylist()
		},
		computed: {
			// ...mapGetters(['citylist'])
		},
		mounted() {
			this.$nextTick(() => {
				let h = 0, hArr = []
				Array.from(document.querySelectorAll('.abc')).forEach(item => {
					h += item.clientHeight
					hArr.push(h)
				})
				this.heightArrList = hArr
			})
		},
		methods: {
			...mapActions([
				// 'getCitylist'
			]),
			judgeToPos(e) {
				console.log('e', e)
			}
		},
		components: {
			AppHeader,
			AppFooter
		}
	}
</script>


<style lang="less" scoped>
	.city-wrapper{
		*{
			font-size: .28rem;
		}
		.location-city{
			padding: .28rem .2rem;
			border-radius: .06rem;
			overflow: hidden;
			background-color: #FDFDFC;
		}
		h4{
			font-size: .3rem;
			margin-top: .2rem;
			margin-bottom: .14rem;
			margin-left: 0.2rem;
		}
		.box{
			margin: 0 0.2rem;
			margin-top: 0.2rem;
			border-radius: .06rem;
			overflow: hidden;
			background-color: #FDFDFC;
			.charlist, .hot-city{
				li{
					display: inline-block;
					width: 20%;
					height: .8rem;
					text-align: center;
					line-height: .8rem;
					font-size: .28rem;
					.color-strong{
						color: #06c1ae!important;
					}
					a.react{
						display: block;
						color: inherit;
						height: 100%;
						overflow-x: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
				}
			}
			.hot-city{
				.table:before {
					content: '';
					position: absolute;
					width: 25%;
					left: 25%;
					height: 100%;
					border-left: 0.5px solid #ddd8ce;
					border-right: 0.5px solid #ddd8ce;
				}

				.table:after {
					content: '';
					position: absolute;
					width: 10%;
					left: 75%;
					height: 100%;
					border-left: 0.5px solid #ddd8ce;
					border-right: none;
				}
			}
			.abc{
				clear: both;
			}
			.table {
					min-height: .8rem;
					position: relative;
					overflow: hidden;
					z-index: 0; 
				}

			.table:before {
				content: '';
				position: absolute;
				width: 25%;
				left: 25%;
				height: 100%;
				border-left: 0.5px solid #ddd8ce;
				border-right: 0.5px solid #ddd8ce;
			}

			.table:after {
				content: '';
				position: absolute;
				width: 10%;
				left: 75%;
				height: 100%;
				border-left: 0.5px solid #ddd8ce;
				border-right: none;
			}

			.table.table-t3:before {
				width: 33.33%;
				left: 33.33%; 
			}

			.table.table-t3:after {
				border: none; 
			}

			.table li,
			.table h4 {
				display: inline-block;
				width: 25%;
				height: .8rem;
				line-height: .8rem;
				font-size: .28rem;
				text-align: center;
				margin-bottom: -1px;
				float: left;
				position: relative;
				z-index: 10; 
			}

			.table.table-t3 li,
			.table.table-t3 h4 {
				width: 33.33%; 
			}

			.table h4 {
				margin: 0;
				margin-bottom: -1px;
				height: 1.6rem;
				line-height: 1.6rem;
				color: #B7B7B7;
				font-size: .8rem; 
			}
			.abc:last-child {
				border-bottom: none;
			}
		}
	}
</style>



